export default {
	data() {
		return {
			navColor: 'white',//导航背景色
			textOpacity: 0, //导航透明度
			userInfo: null, //用户信息
			
			isAuth: false, //未授权
		}
	},
	onLoad() {
		if(this.$userInfo) {
			this.userInfo = this.$userInfo
			this.isAuth = true
		}else {
			this.$api.userLogin((status) => {
				if(status == 1) {
					this.isAuth = true
					this.userInfo = this.$userInfo
				}
			})
		}
	},
	onPageScroll(e) {
		let top = e.scrollTop
		this.textOpacity = top / 200 > 1 ? 1 : top / 200
	},
	methods: {
		getUserInfo: function(e) { //按钮登录授权
		  if (e.detail.errMsg == 'getUserInfo:ok') {
		    uni.showLoading({
		      title: '授权登陆中...',
		      mask: true
		    })
		    const $this = this
		    this.$api.userLogin((status) => {
		      if (status == '1') {
		          uni.hideLoading()
		          uni.showToast({
		            title: '授权登录成功',
		            icon: 'success',
		            mask: true,
		            success: () => {
						$this.userInfo = $this.$userInfo
						$this.isAuth = true
		            }
		          })
		      }
		    })
		  }else {
		    this.$api.warnNotice('您已拒绝授权！')
		    this.isAuth = false
		  }
		},
		toJumpMember() {
			uni.navigateTo({
				url: '/pageMember/utravelMember/utravelMember'
			})
		},
		toJumpMineCollect() { //我的收藏
			if(this.isAuth) {
				uni.navigateTo({
					url: '/pages/collectList/collectList'
				})
			}else {
				this.$api.warnNotice('请先授权登录~')
			}
		},
		toJumpMineOrders() { //我的订单
			if(this.isAuth) {
				uni.navigateTo({
					url: '/pages/myOrders/myOrders'
				})
			}else {
				this.$api.warnNotice('请先授权登录~')
			}
		},
		toJumpMineWallet() { //我的卡包
			if(this.isAuth) {
				uni.navigateTo({
					url: '/pages/mineWallet/mineWallet'
				})
			}else {
				this.$api.warnNotice('请先授权登录~')
			}
		},
		toJumpAddress() { //地址管理
			if(this.isAuth) {
				uni.navigateTo({
					url: '/pages/addressList/addressList'
				})
			}else {
				this.$api.warnNotice('请先授权登录~')
			}
		},
		toJumpMineInfo() { //个人信息
			if(this.isAuth) {
				uni.navigateTo({
					url: '/pages/personalInfo/personalInfo'
				})
			}else {
				this.$api.warnNotice('请先授权登录~')
			}	
		},
		toJumpApplyPartner() { //申请合伙人
			if(this.isAuth) {
				uni.navigateTo({
					url: '/pages/applyPartner/applyPartner'
				})
			}else {
				this.$api.warnNotice('请先授权登录~')
			}
		},
		toJumpIntegralList() { //积分排行
			if(this.isAuth) {
				uni.navigateTo({
					url: '/pages/integralList/integralList'
				})
			}else {
				this.$api.warnNotice('请先授权登录~')
			}
		}
	}
}
